// react-app/src/App.js
import React from 'react';
import SalesChart from './components/SalesChart';
// একটি স্যাম্পল আইকন (পরে আমরা ভালো আইকন লাইব্রেরি ব্যবহার করব)
const MetricIcon = () => (
  <svg className="h-8 w-8 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M13 7h8m0 0v8m0-8l-8 8-4-4-6 6" />
  </svg>
);

// মেট্রিক্স কার্ড কম্পোনেন্ট
function MetricCard({ title, value, icon }) {
  return (
    <div className="bg-white p-6 rounded-lg shadow-md flex items-center space-x-4">
      <div className="bg-gray-100 p-3 rounded-full">
        {icon}
      </div>
      <div>
        <p className="text-sm font-medium text-gray-500">{title}</p>
        <p className="text-2xl font-bold text-gray-800">{value}</p>
      </div>
    </div>
  );
}

function App() {
  // আপাতত ডামি ডেটা ব্যবহার করছি
  const overviewData = {
    totalSales: '৳12,50,345',
    netSales: '৳11,80,120',
    orders: '1,450',
    avgOrderValue: '৳862.30',
  };

  return (
    <div className="bg-gray-50 min-h-full p-4 sm:p-6 lg:p-8 aorw-app-container">
      <div className="max-w-7xl mx-auto">
        {/* হেডার */}
        <header className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900">Order Reports Dashboard</h1>
          <p className="mt-1 text-gray-600">An overview of your store's performance.</p>
        </header>

        {/* মেট্রিক্স গ্রিড */}
        <div className="grid grid-cols-1 gap-5 sm:grid-cols-2 lg:grid-cols-4">
          <MetricCard title="Total Sales" value={overviewData.totalSales} icon={<MetricIcon />} />
          <MetricCard title="Net Sales" value={overviewData.netSales} icon={<MetricIcon />} />
          <MetricCard title="Total Orders" value={overviewData.orders} icon={<MetricIcon />} />
          <MetricCard title="Avg. Order Value" value={overviewData.avgOrderValue} icon={<MetricIcon />} />
        </div>

        {/* চার্ট এবং অন্যান্য কন্টেন্ট এখানে যোগ হবে */}
        <div className="mt-8 bg-white p-6 rounded-lg shadow-md">
            <h2 className="text-xl font-semibold text-gray-800">Sales Over Time</h2>
             <div className="h-96">
                  <SalesChart />
              </div>
        </div>
      </div>
    </div>
  );
}

export default App;